export default {
    name: 'InputListForm',
    props: ['input_list', 'indices'],
    computed: {
        num_elements() {
            return this.input_list ? this.input_list.length : undefined
        }
    },
    methods: {
        handleInput(e) {
            this.$emit('handleInput', Number(e.target.name), e.target.value, this.indices)
        }
    }
}
